.header__top {
  height: 14vh;
  background-color: #196bb1;
}

.header__top .header__inner {
  width: 1170px;
  height: inherit;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-areas: "back logo compare";
  align-items: center;
  margin: 0 auto;
  display: grid;
}

.header__top .header__inner img {
  display: block;
}

.header__top .header__inner .header__logo {
  grid-area: logo;
  margin: 0 auto;
  display: block;
}

.header__top .header__inner .header__logo img {
  height: 9vh;
}

.header__top .header__inner .header__compare {
  grid-area: compare;
  margin-left: auto;
}

.header__top .header__inner .header__compare:hover path {
  fill-opacity: 1;
}

.header__top .header__inner .header__compare svg {
  height: 6vh;
}

.header__top .header__inner .header__compare svg path {
  transition: all .4s;
}

.header__top .header__inner .header__back {
  cursor: pointer;
  background-color: #0000;
  outline: none;
  grid-area: back;
  margin-right: auto;
  display: block;
}

.header__top .header__inner .header__back:hover path {
  stroke-opacity: 1;
}

.header__top .header__inner .header__back svg {
  height: 4vh;
}

.header__top .header__inner .header__back svg path {
  transition: all .4s;
}

.header__top .header__inner .header__hidden {
  display: none;
}

